//@flow
import React from 'react';
import { Formik } from 'formik';
import { t } from '../../../i18n';
import * as Yup from 'yup';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';
const RESET_PASSWORD_NAMESPACE = 'Login:resetPassword.';
const validationSchema = Yup.object().shape({
  identification: Yup.string().required(
    t(`${RESET_PASSWORD_NAMESPACE}phoneNumberRequiredMsg`),
  ),
  email: Yup.string()
    .email()
    .required(t(`${RESET_PASSWORD_NAMESPACE}emailRequiredMsg`)),
  accountNumber: Yup.string().required(
    t(`${RESET_PASSWORD_NAMESPACE}accountNumberRequiredMsg`),
  ),
});
const initialValues = {
  identification: '',
  email: '',
  accountNumber: '',
};

type Props = {
  active: boolean,
  submitStepRequest: Function,
  handleCancel: Function,
  setFormRef: Function,
};
const ResetPasswordStep1 = (props: Props) => {
  let className = props.active ? 'step-pane active' : 'step-pane';

  return (
    <div className={className} data-step="1">
      <Formik
        ref={ref => props.setFormRef(ref)}
        validationSchema={validationSchema}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          props.submitStepRequest(values);
        }}
        initialValues={initialValues}
      >
        {formikProps => {
          const {
            values,
            handleChange,
            handleSubmit,
            handleBlur,
            isValid,
          } = formikProps;
          return (
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <input
                  className="form-control"
                  name="identification"
                  type="text"
                  value={values.identification}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(
                    `${RESET_PASSWORD_NAMESPACE}identificationPlaceholder`,
                  )}
                />
                <ExpiringFormFieldError name="identification" />
              </div>
              <div className="form-group">
                <input
                  className="form-control"
                  name="email"
                  type="email"
                  value={values.email}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(`${RESET_PASSWORD_NAMESPACE}emailPlaceholder`)}
                />
                <ExpiringFormFieldError name="email" />
              </div>
              <div className="form-group">
                <input
                  className="form-control"
                  name="accountNumber"
                  type="text"
                  value={values.accountNumber}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(
                    `${RESET_PASSWORD_NAMESPACE}accountNumberPlaceholder`,
                  )}
                />
                <ExpiringFormFieldError name="accountNumber" />
              </div>
              <div className="form-group">
                <div className="align-right text-right">
                  <button
                    className="btn btn-default btn-space"
                    onClick={props.handleCancel}
                    type="button"
                  >
                    {t(`${RESET_PASSWORD_NAMESPACE}cancelBtn`)}
                  </button>
                  <button
                    className="btn btn-secondary btn-space wizard-next"
                    data-wizard="#wizard1"
                    onClick={handleSubmit}
                    disabled={!isValid}
                  >
                    {t(`${RESET_PASSWORD_NAMESPACE}nextBtn`)}
                  </button>
                </div>
              </div>
            </form>
          );
        }}
      </Formik>
    </div>
  );
};

export default ResetPasswordStep1;
