import React from 'react';
import Expire from '../Expire/Expire';
import FormFieldError from './FormFieldError';
type Props = {
  name: string,
  delay: Number,
};
const ExpiringFormFieldError = (props: Props) => (
  <Expire delay={props.delay}>
    <FormFieldError name={props.name} />
  </Expire>
);
ExpiringFormFieldError.defaultProps = {
  delay: 3000,
};
export default ExpiringFormFieldError;
