//@flow
import React from 'react';
import { Formik } from 'formik';
import { t } from '../../../i18n';
import * as Yup from 'yup';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';
const RESET_PASSWORD_NAMESPACE = 'Login:resetPassword.';
const validationSchema = Yup.object().shape({
  codeOtp: Yup.string().required(
    t(`${RESET_PASSWORD_NAMESPACE}otpRequiredMsg`),
  ),
  newPassword: Yup.string()
    .required(t(`${RESET_PASSWORD_NAMESPACE}newPasswordRequiredMsg`))
    .min(8, t(`${RESET_PASSWORD_NAMESPACE}passwordMinCharactersMsg`))
    .max(12, t(`${RESET_PASSWORD_NAMESPACE}passwordMaxCharactersMsg`)),
  passwordConfirm: Yup.string()
    .required(t(`${RESET_PASSWORD_NAMESPACE}confirmationPasswordRequiredMsg`))
    .oneOf(
      [Yup.ref('newPassword'), null],
      t(`${RESET_PASSWORD_NAMESPACE}passwordShouldMatchMsg`),
    ),
});
const initialValues = {
  codeOtp: '',
  newPassword: '',
  passwordConfirm: '',
};
type Props = {
  active: boolean,
  submitStepRequest: Function,
  handlePrecedent: Function,
  setFormRef: Function,
};
const ResetPasswordStep2 = (props: Props) => {
  let className = props.active ? 'step-pane active' : 'step-pane';
  return (
    <div className={className} data-step="1">
      <Formik
        ref={ref => props.setFormRef(ref)}
        validationSchema={validationSchema}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          props.submitStepRequest(values);
        }}
        initialValues={initialValues}
        enableReinitialize
      >
        {formikProps => {
          const {
            values,
            handleChange,
            handleBlur,
            handleSubmit,
            isValid,
          } = formikProps;
          return (
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <input
                  className="form-control"
                  name="codeOtp"
                  type="text"
                  value={values.codeOtp}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(`${RESET_PASSWORD_NAMESPACE}otpPlaceholder`)}
                />

                <ExpiringFormFieldError name="codeOtp" />
              </div>
              <div className="form-group">
                <input
                  className="form-control"
                  name="newPassword"
                  type="password"
                  value={values.newPassword}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(
                    `${RESET_PASSWORD_NAMESPACE}newPasswordPlaceholder`,
                  )}
                />
                <ExpiringFormFieldError name="newPassword" />
              </div>
              <div className="form-group">
                <input
                  className="form-control"
                  name="passwordConfirm"
                  type="password"
                  value={values.passwordConfirm}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(
                    `${RESET_PASSWORD_NAMESPACE}newPasswordConfirmPlaceholder`,
                  )}
                />
                <ExpiringFormFieldError name="passwordConfirm" />
              </div>

              <div className="form-group">
                <div className="align-right text-right">
                  <button
                    className="btn btn-default btn-space wizard-previous"
                    data-wizard="#wizard1"
                    onClick={props.handlePrecedent}
                  >
                    {t(`${RESET_PASSWORD_NAMESPACE}precedentBtn`)}
                  </button>
                  <button
                    className="btn btn-secondary btn-space wizard-next"
                    data-wizard="#wizard1"
                    onClick={handleSubmit}
                    disabled={!isValid}
                  >
                    {t(`${RESET_PASSWORD_NAMESPACE}validateBtn`)}
                  </button>
                </div>
              </div>
            </form>
          );
        }}
      </Formik>
    </div>
  );
};

export default ResetPasswordStep2;
