import React from 'react';
import LoginHeader from './components/LoginHeader';
import { Link } from 'react-router-dom';
import LoginForm from './components/LoginForm';
import { t } from '../../i18n';

const Login = () => (
  <div className="page page-login">
    <div className="page-content">
      <div className="page-header-login">
        <LoginHeader />
      </div>
      <div className="content">
        <div className="login-container">
          <div className="bcp-wrapper bcp-login">
            <div className="bcp-content">
              <div className="main-content container-fluid">
                <div className="connexion">
                  <div className="left__col">
                    <div className="img__lock align-center xs-mb-30">
                      <img
                        className="logo-lock "
                        src={require('../../assets/img/lock.svg')}
                        alt="searchBar"
                        width="80"
                      />
                    </div>
                    <h2>{t('Login:securityTips')}</h2>
                    <h3>{t('Login:isItyourBankSite')}</h3>
                    <p>{t('Login:verifyBankSite')}</p>
                    <img
                      className="logo-barre"
                      src={require('../../assets/img/barre.png')}
                      alt="searchBar"
                    />
                    <h3>{t('Login:yourPassword')}</h3>
                    <div className="link">
                      <p>{t('Login:accountPersonalAccess')}</p>
                      <li>
                        <Link to="#">{t('Login:tipsDiscover')}</Link>
                      </li>
                      <p>{t('Login:connexionDifficulties')}</p>
                      <li>
                        <Link to="#">{t('Login:contactUs')}</Link>
                      </li>
                    </div>
                  </div>
                  <div className="right__col">
                    <div className="panel panel-default panel-border-color-primary">
                      <div className="panel-heading">
                        <h2>
                          <img
                            className="logo-user "
                            src={require('../../assets/img/user.svg')}
                            alt="userLogo"
                            width="40"
                          />
                          {t('Login:memberAuthenticate')}
                        </h2>
                        <span className="splash-description">
                          {t('Login:enterCredentials')}
                        </span>
                      </div>
                      <LoginForm />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default Login;
