import React, { Component } from 'react';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import {
  resetPassword,
  confirmResetPassword,
  toggleResetPassword,
  previousResetPasswordStep,
} from '../../../redux/login/login.actions';
import FormModal from '../../../components/Ui/CustomModel/FormModal';
import ResetPasswordStep1 from './ResetPasswordStep1';
import ResetPasswordStep2 from './ResetPasswordStep2';
import Alert from '../../../components/Ui/Alert/Alert';
import Expire from '../../../components/Ui/Expire/Expire';
const RESET_PASSWORD_NAMESPACE = 'Login:resetPassword.';
class ResetPassword extends Component {
  state = {
    phone: '',
    mail: '',
    showAlert: false,
  };

  firstStepFormRef = null;
  _setFirstStepFormRef = ref => {
    this.firstStepFormRef = ref;
  };
  secondStepFormRef = null;
  _setSecondStepFormRef = ref => {
    this.secondStepFormRef = ref;
  };
  _submitFirstStepRequest = values => {
    this.props.sendResetPasswordRequest({
      phone: values.identification,
      mail: values.email,
      accountNumber: values.accountNumber,
    });
    this.setState({
      phone: values.identification,
      mail: values.email,
    });
  };
  _submitSecondStepRequest = values => {
    this.props.sendConfirmationResetPassword({
      phone: this.state.phone,
      mail: this.state.mail,
      hash: this.props.hash,
      activityId: this.props.activityId,
      password: values.newPassword,
      otp: values.codeOtp,
    });
  };
  _handlePrecedent = () => {
    this.props.previousResetPasswordStep();
    this.secondStepFormRef.resetForm();
  };

  _triggerNext = () => this.props.activeStep === 2;

  _initState = () => {
    this.setState({
      phone: '',
      mail: '',
    });
  };
  _handleClose = () => {
    this.props.handleClose();
    this._initState();
    this.firstStepFormRef.resetForm();
    this.secondStepFormRef.resetForm();
  };
  _renderAlert = () => {
    if (this.state.showAlert) {
      return (
        <Expire
          delay={3000}
          callback={() => this.setState({ showAlert: false })}
        >
          <Alert
            type="Default Danger"
            text={this.props.error}
            className="animated"
          />
        </Expire>
      );
    }
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.error !== this.props.error && nextProps.error) {
      this.setState({ showAlert: true });
    }
  }
  render() {
    return (
      <FormModal
        title={t(`${RESET_PASSWORD_NAMESPACE}modelTitle`)}
        show={this.props.showResetPasswordModal}
        handleClose={this._handleClose}
        alert={this._renderAlert}
      >
        <div className="row wizard-row">
          <div className="col-md-12 fuelux">
            <div className="block-wizard panel panel-default">
              <div className="wizard wizard-ux no-steps-container" id="wizard1">
                <ul className="steps" style={{ marginLeft: '0' }}>
                  <li
                    className={this._triggerNext() ? 'complete' : 'active'}
                    data-step="1"
                  >
                    {t(`${RESET_PASSWORD_NAMESPACE}firstStepTitle`)}
                    <span className="chevron"></span>
                  </li>
                  <li
                    className={this._triggerNext() ? 'active' : ''}
                    data-step="2"
                  >
                    {t(`${RESET_PASSWORD_NAMESPACE}secondStepTitle`)}
                    <span className="chevron"></span>
                  </li>
                </ul>
                <div className="step-content">
                  <ResetPasswordStep1
                    active={!this._triggerNext()}
                    submitStepRequest={this._submitFirstStepRequest}
                    handleCancel={this._handleClose}
                    setFormRef={this._setFirstStepFormRef}
                  />
                  <ResetPasswordStep2
                    active={this._triggerNext()}
                    handlePrecedent={this._handlePrecedent}
                    submitStepRequest={this._submitSecondStepRequest}
                    setFormRef={this._setSecondStepFormRef}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </FormModal>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  sendResetPasswordRequest: resetPasswordData => {
    dispatch(resetPassword(resetPasswordData));
  },
  sendConfirmationResetPassword: confirmationPasswordData => {
    dispatch(confirmResetPassword(confirmationPasswordData));
  },
  handleClose: () => {
    dispatch(toggleResetPassword());
  },
  previousResetPasswordStep: () => {
    dispatch(previousResetPasswordStep());
  },
});
const mapStateToProps = state => ({
  error: state.Login.error,
  isLoading: state.Login.isLoading,
  hash: state.Login.hash,
  activityId: state.Login.activityId,
  showResetPasswordModal: state.Login.showResetPasswordModal,
  activeStep: state.Login.activeStep,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResetPassword);
