import React from 'react';
import { Link } from 'react-router-dom';
import HomeIcon from '../../../assets/SvgComponents/HomeSvg/HomeIcon';
import Logo from '../../../components/Logo/rotatedLogo';

const LoginHeader = () => (
  <header>
    <nav className="navbar navbar-default navbar-fixed-top bcp-top-header">
      <div className="container-fluid">
        <div className="navbar-header">
          <div className="c-header-icon-home">
            <Link to="#">
              <HomeIcon />
            </Link>
          </div>
          <Link className="navbar-brand" to="#">
            <Logo />
          </Link>
        </div>
      </div>
    </nav>
  </header>
);

export default LoginHeader;
